<template>
  <!-- 当数据请求到了之后在显示 -->
  <view v-if="goods_info.goods_name" class="goods-detail-container">
    <!-- 轮播图区域 -->
    <swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000">
      <swiper-item v-for="(item,i) in goods_info.pics" :key="i">
       <image :src="item.pics_big" mode="widthFix" @click="preview(i)"></image>
      </swiper-item>
    </swiper>
    
    <!-- 商品信息区域 -->
    <view class="goods-info-box">
       <!-- 商品价格 -->
       <view class="price">
         ¥ {{goods_info.goods_price}}
       </view>
       <!-- 商品信息主题区域 -->
       <view class="goods-info-body">
         <view class="goods-name">{{goods_info.goods_name}}</view>
         <!-- 收藏 -->
         <view class="favi">
           <uni-icons type="star" size="18" color="gray"></uni-icons>
           <text>收藏</text>
         </view>
       </view>
       <!--  运费-->
       <view class="yf">快递：免运费</view>
    </view>
   <!-- 渲染商品详情信息 nodes属性解析html文本 -->
   <rich-text :nodes="goods_info.goods_introduce"></rich-text>
   
   <!-- 商品 导航组件区域-->
   <view class="goods_nav">
     <!-- fill 控制右侧按钮的样式 -->
     <!-- options 左侧按钮的配置项 -->
     <!-- buttonGroup 右侧按钮的配置项 -->
     <!-- click 左侧按钮的点击事件处理函数 -->
     <!-- buttonClick 右侧按钮的点击事件处理函数 -->
     <uni-goods-nav :fill="true" :options="options" :buttonGroup="buttonGroup" @click="onClick" @buttonClick="buttonClick" />
   </view>
  </view>
</template>

<script>
  import {mapState, mapMutations ,mapGetters} from 'vuex'
  export default {
    computed:{
      // 把 m_cart 模块中的 addToCart 方法映射到当前页面使用
      // ...mapState('m_cart',['cart'])
      // ...mapState({cart:state=>state.moduleCart.cart})
      ...mapGetters('m_cart',['total'])
    },
    watch:{
      // total(newVal){
      //   const findResult = this.options.find(x => x.text === '购物车')
      //   if(findResult){
      //     findResult.info = newVal
      //   }
      // }
      // 定义 total 侦听器，指向一个配置对象 
     total: {
        // handler 属性用来定义侦听器的 function 处理函数
        handler(newVal) {
           const findResult = this.options.find(x => x.text === '购物车')
           if (findResult) {
              findResult.info = newVal
           }
        },
        // immediate 属性用来声明此侦听器，是否在页面初次加载完毕后立即调用
        immediate: true
       }
    },
    data() {
      return {
        goods_info:{},
        // 左侧按钮组的配置对象
        options: [{
          icon: 'shop',
          text: '店铺'
        }, {
          icon: 'cart',
          text: '购物车',
          info: 0
        }],
        // 右侧按钮组的配置对象
        buttonGroup: [{
            text: '加入购物车',
            backgroundColor: '#ff0000',
            color: '#fff'
          },
          {
            text: '立即购买',
            backgroundColor: '#ffa200',
            color: '#fff'
          }
        ]
      };
    },
    onLoad(option) {
      const goods_id = option.goods_id
      this.getGoodsDetail(goods_id)
    },
    methods:{
      ...mapMutations('m_cart',['addToCart']),
      async getGoodsDetail(goods_id){
        const {data:res} = await uni.$http.get('/api/public/v1/goods/detail',{goods_id:goods_id})//这里可以简写
        // 使用字符串的 replace() 方法，将 webp 的后缀名替换为 jpg 的后缀名
          res.message.goods_introduce = res.message.goods_introduce.replace(/<img /g, '<img style="display:block;" ').replace(/webp/g, 'jpg')
        if(res.meta.status !== 200) return uni.$showMsg()
        this.goods_info = res.message
      },
      //实现轮播图的预览
      preview(i){
        uni.previewImage({
          // 预览时，默认显示图片的索引
          current: i,
          // 所有图片 url 地址的数组
          urls:this.goods_info.pics.map(x => x.pics_big)
        })
      },
      //切换到购物车额界面
      onClick(e){
        if(e.content.text === '购物车'){
          uni.switchTab({
            url:'/pages/cart/cart'
          })
        }
      },
      //当点击了加入购物车或者立即购买按钮之后
      buttonClick(e){
        if(e.content.text === '加入购物车'){
          //组织商品的信息对象
          const goods = {
            goods_id: this.goods_info.goods_id,
            goods_name:this.goods_info.goods_name,
            goods_price:this.goods_info.goods_price,
            goods_count:1,
            goods_small_logo:this.goods_info.goods_small_logo,
            goods_state:true
          }
          //调用addToCart方法
          this.addToCart(goods)
        }
      }
    }
  }
</script>

<style lang="scss">
swiper{
  height: 650rpx;
  image{
    width: 100%;
    height: 100%;
  }
}
.goods-info-box{
  padding: 10px;
  .price{
    font-size: 18px;
    color: #C00000;
    margin: 10px 0;
  }
  .goods-info-body{
    display: flex;
    justify-content: space-between;
    .goods-name{
      font-size: 13px;
      margin-right: 10px;
    }
    .favi{
      display: flex;
      width: 120px;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 12px;
      border-left: 1px solid #efefef;
      color:gray
    }
  }
  .yf{
    margin: 10px 0;
    font-size: 12px;
    color: gray;
  }
}
.goods_nav{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
.goods-detail-container{
  padding-bottom: 50px;
}
</style>
